import styled from "@emotion/styled";

// 1.   .

export const ProblemSetList = styled.div`
  width: 25%;
  padding: 20px;
  background-color: #f8f9fa;
  border-right: 1px solid #dee2e6;
  height: 100%;
`;

export const ProblemImage = styled.img`
  width: 100%;
  max-width: 500px;
  margin-bottom: 20px;
  border: 1px solid #ced4da;
  border-radius: 4px;
`;

// 4.      !
export const ProblemSideList = styled.div`
  width: 25%;
  padding: 20px;
  background-color: #f0f0f8;
  border-left: 1px solid #dee2e6;
  overflow-y: auto;
  height: 100%;
`;

export const ProblemSideItem = styled.div`
  margin-bottom: 10px;
  padding: 10px;
  background-color: #fff;
  border: 1px solid #ced4da;
  border-radius: 4px;
  cursor: pointer;

  &:hover {
    background-color: #e9ecef;
  }
`;

export const ProblemSideItemContent = styled.div`
  font-size: 14px;
  color: #495057;
`;

// 5.    !
export const CommentSection = styled.div`
  padding: 20px;
  background-color: #f8f9fa;
  border-top: 1px solid #dee2e6;
`;

export const CommentInput = styled.textarea`
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ced4da;
  border-radius: 4px;
  resize: vertical;
`;

export const CommentButton = styled.button`
  background-color: #28a745;
  color: #fff;
  border: none;
  padding: 8px 15px;
  border-radius: 4px;
  cursor: pointer;
`;

export const AppContainer = styled.div`
  display: flex;
  height: 100vh; //  100vh 
`;

export const Header = styled.header`
  background-color: #fff;
  padding: 10px 20px;
  border-bottom: 1px solid #dee2e6;
  display: flex;
  justify-content: space-between;
  align-items: center;

  h1 {
    font-size: 1.5rem;
    font-weight: bold;
  }
`;

export const ChoiceButton = styled.button`
  background-color: ${(props) => (props.isSelected ? "skyblue" : "white")};
  border: 1px solid black;
  width: 15px; /*    */
  height: 15px; /*    (  ) */
  padding: 0; /*    (   ) */
  cursor: pointer;
  border-radius: 50%; /*   */
  display: flex; /*     flexbox */
  align-items: center; /*    */
  justify-content: center; /*    */
  font-size: 14px; /*    
  margin-right: 100px;


  &:hover {
    background-color: lightgray;
  }
`;

export const SubmitButton = styled.button`
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  margin-top: 20px;

  &:hover {
    background-color: #0069d9;
  }
`;

export const ResultList = styled.ul`
  list-style: none;
  padding: 0;
  margin-top: 10px;
`;

export const ResultItem = styled.li`
  padding: 10px;
  border-bottom: 1px solid #eee;
  cursor: pointer;
  background-color: ${(props) => (props.isSelected ? "#f5f5f5" : "white")};

  &:hover {
    background-color: #f5f5f5;
  }
`;

export const ScoreDisplay = styled.div`
  padding: 10px 20px;
  margin-bottom: 20px; // ExamProblem 
  border: 1px solid #007bff; //  
  border-radius: 4px;
  background-color: #f0f8ff; //   
  font-size: 18px;
  font-weight: bold;
  color: #007bff; //  
  text-align: center; //   
`;

export const ProblemsContainer = styled.div`
  display: flex;
  flex-direction: column;
  width: 50%;
  gap: 20px;
  padding: 20px;
`;
